import { NavHashLink } from 'react-router-hash-link';
import { useState, useEffect, useRef } from 'react';
import '../../styles/Header.css';

function Header() {
  const [isTablet, setIsTablet] = useState(false);
  const [openBurgerMenu, setOpenBurgerMenu] = useState(false);

  const burgerMenuRef = useRef(null);

  // Cration d'une fonction qui configure le useState en fonction d'une taill d'cran
  const handleResize = () => {
    setIsTablet(window.innerWidth < 1240);
  };

  useEffect(() => {
    handleResize();
    window.addEventListener('resize', handleResize); //ajout d'un vnement 
    return () => {
      window.removeEventListener('resize', handleResize); // suppression de l'vnement
    };
  }, []);

  const handleBurgerMenu = () => {
    setOpenBurgerMenu(!openBurgerMenu);
  };

  useEffect(() => {
    const handleClickOutside = (event) => {
        if(burgerMenuRef.current && !burgerMenuRef.current.contains(event.target)) {
            setOpenBurgerMenu(false);
        };
    }

        if(openBurgerMenu) {
            document.addEventListener('mousedown', handleClickOutside);
        } else {
            document.removeEventListener('mousedown', handleClickOutside);
        };
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    
  }, [openBurgerMenu]);

  return (
    <header>
      <div className='header-container'>
        <div className='img-container'>
          <NavHashLink smooth to='/#section-presentation'>
            <img src='/assets/images/illustration-rmi.webp' alt="illustration de Rmi" />
          </NavHashLink>
        </div>
        <nav className='navbar'>
          {!isTablet ? (
            <ul className='nav-links'>
              <li><NavHashLink smooth to='/#about'> propos</NavHashLink></li>
              <li><NavHashLink smooth to='/#skills'>Comptences</NavHashLink></li>
              <li><NavHashLink smooth to='/#project'>Projets</NavHashLink></li>
              <li><NavHashLink smooth to='/#contact'>Contact</NavHashLink></li>
            </ul>
          ) : (
            <div ref={burgerMenuRef}>
              <button onClick={ handleBurgerMenu }>
                <img id='burger-menu-icon' src="/assets/icons/burger-bar.png" alt="Menu burger" />
              </button>
                <ul className={`nav-links-mobile ${openBurgerMenu ? 'open' : ''}`}>                 
                <li><NavHashLink smooth to='/#about' onClick={handleBurgerMenu}> propos</NavHashLink></li>
                  <li><NavHashLink smooth to='/#skills' onClick={handleBurgerMenu}>Comptences</NavHashLink></li>
                  <li><NavHashLink smooth to='/#project' onClick={handleBurgerMenu}>Projets</NavHashLink></li>
                  <li><NavHashLink smooth to='/#contact' onClick={handleBurgerMenu}>Contact</NavHashLink></li>
                </ul>
              
            </div>
          )}
        </nav>
      </div>
    </header>
  );
}

export default Header;